.cartscreen {
    display: flex;
    max-width: 1300px;
    margin: 2rem auto;
  }
  
  .cartscreen h2 {
    margin-bottom: 1rem;
  }
  
  .cartscreen__left {
    flex: 0.7;
    margin-right: 1rem;
    background: transparent;
    padding: 1rem;
  }
  
  .cartscreen__right {
    flex: 0.3;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    height: fit-content;
    margin-top: 62px;
  }
  
  .cartscreen__right div {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1rem;
  }
  
  .cartscreen__info p {
    padding: 8px;
  }
  
  .cartscreen__info p:first-child {
    font-weight: bold;
  }
  
  .cartscreen__right div:last-child {
    border: none;
  }
  
  .cartscreen__right div button {
    border-color: #FEBE10;
    border-radius: 0;
    color: #171717;
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: color 150ms ease-in-out;
  }
  
  .cartscreen__right div button:hover {
    opacity: 0.9;
    background-color: #FEBE10;
    color: #171717;
  }

  .cartscreen__left div > .btn {
    text-decoration: none;
    border-color: #FEBE10;
    border-radius: 0;
    color: #171717;
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: color 150ms ease-in-out;
    margin-top: 10px;
    width: 200px;;
  }

  .cartscreen__left div > .btn:hover {
    opacity: 0.9;
    background-color: #FEBE10;
    color: #171717;
  }

  
  
  @media (max-width: 1320px) {
    .cartscreen {
      max-width: 900px;
    }
  }
  
  @media (max-width: 960px) {
    .cartscreen {
      max-width: 800px;
    }
  }
  
  @media (max-width: 960px) {
    .cartscreen {
      flex-direction: column;
    }
  
    .cartscreen__right {
      margin: 1rem;
    }
  }
  